html, body {
    min-height: 100%;
    margin: 0;
    padding: 0;
    background-color: white;
    scroll-behavior: smooth;
}

body > header{
    position: absolute;
    top:0;
    left:0;
    opacity:0;
    user-select: none;
}

@media (prefers-color-scheme: dark) {

    html,body{
        background-color: black;
    }
}